import { authenticationServer } from '../api/authentication';
import router from '../router/router';

const mutations = {

  login: (state, { token, userLogin }) => {
    state.token = token;
    state.userLogin = userLogin;

    localStorage.setItem('token', token);
  },

  logout: (state) => {
    state.token = null;
    state.userLogin = null;

    localStorage.removeItem('token');
  },

};

const actions = {
  authentication: ({ commit }, { userLogin, password }) => {
    authenticationServer(userLogin, password)
      .then((res) => {
        // eslint-disable-next-line
        const token = res.token;
        if (token) {
          commit('login', { token, userLogin });
          router.push({ path: '/main' });
        }
      });
  },

  logout: ({ commit }) => {
    commit('logout');
    router.push({ path: '/login' });
  },

};

const getters = {
  getUser: ({ userLogin }) => userLogin,
};

const state = {
  userLogin: null,
  token: null,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
